import React, { useState } from 'react';
import { Link, useNavigate } from 'react-router-dom';
import { useAuth } from './AuthContext';
import { useUI } from './UIContext';
import { AppBar, Toolbar, Typography, Button, Box, Menu, MenuItem } from '@mui/material';

const Header = () => {
  const { isAuthenticated, user, logout } = useAuth();
  const { openReservationForm } = useUI();
  const navigate = useNavigate();

  const [anchorEl, setAnchorEl] = useState<null | HTMLElement>(null);
  const open = Boolean(anchorEl);

  const handleMenuClick = (event: React.MouseEvent<HTMLButtonElement>) => {
    setAnchorEl(event.currentTarget);
  };

  const handleMenuClose = () => {
    setAnchorEl(null);
  };

  const handleLogout = () => {
    logout();
    navigate('/login');
    handleMenuClose();
  };

  const handleOpenReservationForm = () => {
    openReservationForm();
    handleMenuClose();
  };

  const handleOpenBlockedSlotForm = () => {
    navigate('/blocked');
    handleMenuClose();
  };

  const handleUserManagement = () => {
    navigate('/users');
    handleMenuClose();
  };

  return (
    <AppBar position="static">
      <Toolbar>
        <Typography variant="h6" component="div" sx={{ flexGrow: 1 }}>
          <Link to="/" style={{ textDecoration: 'none', color: 'inherit' }}>
            
          </Link>
        </Typography>
        {isAuthenticated ? (
          <Box sx={{ display: 'flex', alignItems: 'center' }}>
            <Button
              color="inherit"
              id="basic-button"
              aria-controls={open ? 'basic-menu' : undefined}
              aria-haspopup="true"
              aria-expanded={open ? 'true' : undefined}
              onClick={handleMenuClick}
            >
              {user?.name}
            </Button>
            <Menu
              id="basic-menu"
              anchorEl={anchorEl}
              open={open}
              onClose={handleMenuClose}
              MenuListProps={{
                'aria-labelledby': 'basic-button',
              }}
            >
              {user?.role !== 'viewer' && (
                <MenuItem onClick={handleOpenReservationForm}></MenuItem>
              )}
              {user?.role !== 'viewer' && (
                <MenuItem onClick={handleOpenBlockedSlotForm}></MenuItem>
              )}
              {user?.role === 'admin' && (
                <MenuItem onClick={handleUserManagement}></MenuItem>
              )}
              <MenuItem onClick={() => navigate('/manual/general')}></MenuItem>
              {user?.role === 'admin' && (
                <MenuItem onClick={() => navigate('/manual/admin')}></MenuItem>
              )}
              <MenuItem onClick={handleLogout}></MenuItem>
            </Menu>
          </Box>
        ) : (
          <Button color="inherit" component={Link} to="/login"></Button>
        )}
      </Toolbar>
    </AppBar>
  );
};

export default Header;
